.landing_page{
    width: 100vw;
    height: 100vh;

    &.about{
        height: 300vh;
    }
    &.contact{
        height: 80vh;
    }
    &.projects{
        height: fit-content;
    }
}

.background{
    width: 100%;
    height: 100%;
    // background-image: url(../../assets/images/the_boys.jpg);
    background-color: #282c38;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;

    .modal {
        height: 100%;
        width: 100%;
    }
    @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
        .modal {
          -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
          background-color: rgba(20, 20, 20, 0.5);  
        }
        .warning {
          display: none;
        }
    }
}
.landing_title{
    padding-left: 5vw;
    padding-top: 10vh;
    text-align: start;
    animation: color-change 10s infinite alternate;
    font-size: 50px;
    font-weight: 400;
    color: rgb(40, 88, 90);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.landing_page_content{
    height: 100%;
    width: 100%;
    text-align: center;

    
    @keyframes color-change {
        0% { color: #A88740; }
        25% {color: #F9E0AC}
        50% {color: #A0A63F;}
        100% { color: #A84040 }
    }

    .signature_container{
        display: flex;
        justify-content: center;
    }

    .landing_description{
        padding-top: 30px;
        font-size: 25px;
        font-weight: 400;
        color: rgb(198, 209, 209);
    }
}


